import { RESIZE_WINDOW, SET_CONSTANT_DATA } from "../actionTypes";

const initialState = {
  width: 0,
  height: 0,
  constants: {
    "PANELS": {},
    "INVERTERS": {},
  }
};

export default function(state = initialState, action) {
  switch (action.type) {
    case RESIZE_WINDOW: {
      return {
        ...state,
        width: action.payload.width,
        height: action.payload.height,
      };
    }
    case SET_CONSTANT_DATA: {
      return {
        ...state,
        constants: action.payload.data,
      };
    }
    default:
      return state;
  }
}
