function useDebounce(value, delay) {
  const [debouncedValue, setDebouncedValue] = useState(value);

  useEffect(
    () => {
      const handler = setTimeout(() => {
        setDebouncedValue(value);
      }, delay);

      return () => {
        clearTimeout(handler);
      };
    },
    [value]
  );

  return debouncedValue;
}

function MapBox(params) {
  let _map = useRef(null);
  let _deck = useRef(null);
  const {
    mapboxApiAccessToken,
    viewport,
    mapboxReady,
    stepsList,
    mapboxViewportChange,
    setActiveStep,
    setStepMode,
    setStepData,
    setStepParams,
    getPVWatts,
    isMapboxReady,
    deletePVWatts,
    currentStep,
    geocodeResult,
    addLeadData,
    startRegion,
    setErrorMessage,
    setHelpMessage
  } = params;

  const [marker, setMarker] = useState(null);

  useEffect(
    () => {
      if(marker !== null){
        addLeadData({ 'Coordinates': JSON.stringify(marker) });
      }
    },
    [marker]
  );

  const [roofDrawed, setRoofDrawed] = useState(false);
  const [isShowHelpMessages, setIsShowHelpMessages] = useState(true);
  const [editorData, setEditorData] = useState({ type: 'FeatureCollection', features: [] });
  const [panelsData, setPanelsData] = useState({ type: 'FeatureCollection', features: [] });
  const [newPanelData, setNewPanelData] = useState(null);
  const [_bearingLines, setBearingLines] = useState({});
  const [_selectedLines, setSelectedLines] = useState({});
  const [selectedLineFeatureIndex, setSelectedLineFeatureIndex] = useState(null);
  const [_rotatePoints, setRotatePoints] = useState([]);
  const [_modeHandler, setModeHandler] = useState(null);
  const [_PVWattsQueries, setPVWattsQueries] = useState([]);
  const [_panelsHovering, setPanelsHovering] = useState(false);
  const [_deletedPanels, setDeletedPanels] = useState([]);
  const [selectedPanels, setSelectedPanels] = useState([]);
  const [selectedPanelsUpdateTrigger, setSelectedPanelsUpdateTrigger] = useState(false);
  const [cachedPanelPosition, setCachedPanelPosition] = useState(null);

  useEffect(() => {
    if(stepsList[5].params.mode === "FINISH_MODE"){
      setSelectedPanels([]);
    }
  }, [stepsList[5].params.mode]);

  useEffect(() => {
    if(selectedPanelsUpdateTrigger){
      setSelectedPanelsUpdateTrigger(false);
    }
    setStepParams({ params: { selectedPanelsIndexes: selectedPanels }, stepIndex: 5 });
  }, [selectedPanelsUpdateTrigger, selectedPanels]);

  useEffect(() => {
    if(stepsList[5].params.deletePanelsAction){
      setPanelsData(current_panelsData => {
        return { type: 'FeatureCollection', features: _.filter(current_panelsData.features, (f, key) => _.findIndex(selectedPanels, (id) => id === key) === -1) };
      });
      setSelectedPanels([]);
      setStepParams({ params: { deletePanelsAction: false }, stepIndex: 5 });
      editorFeatureUpdate({ updatedData: editorData, editType: "panelsChanged", newPanelsData: { type: 'FeatureCollection', features: _.filter(panelsData.features, (f, key) => _.findIndex(selectedPanels, (id) => id === key) === -1) } });
    }
  },[stepsList[5].params.deletePanelsAction]);

  const debouncedPVWatts = useDebounce(_PVWattsQueries, 1000);

  useEffect(() => {
    if(editorData.features.length > 0){
      setIsShowHelpMessages(false);
    }
  }, [panelsData]);

  useEffect(
    () => {
      if(debouncedPVWatts.length > 0){
        _.map(debouncedPVWatts, (query) => {
          if( query &&
              query.coords &&
              typeof query.stepIndex == 'number' &&
              typeof query.system_capacity == 'number' &&
              typeof query.module_type == 'number' &&
              typeof query.losses == 'number' &&
              typeof query.array_type == 'number' &&
              typeof query.tilt == 'number' &&
              typeof query.azimuth == 'number' &&
              typeof query.inv_eff == 'number' &&
              typeof query.panels_count == 'number'
          ){
            getPVWatts(query);
          }
        });
        setPVWattsQueries([]);
      }
    },
    [debouncedPVWatts]
  );

  useEffect(() => {
    setMapboxRef(_map);
  }, [_map]);

  useEffect(() => {
    if(geocodeResult){
      const resultBBox = geocodeResult.geometry.viewport;
      const bbox = [ [resultBBox.northeast.lng, resultBBox.northeast.lat], [resultBBox.southwest.lng, resultBBox.southwest.lat] ];

      const flyToProps = _map.current.getMap().cameraForBounds(bbox, {
        padding: {top: 0, bottom: 0, left: 0, right: 0}
      });

      const center = [flyToProps.center.lng, flyToProps.center.lat];

      setMarkerPosition(center, flyToProps.zoom);
    }
  }, [geocodeResult]);

  useEffect(() => {
    if(roofDrawed){
      const bboxArray = turf.bbox(roofDrawed);

      const bbox = [ [bboxArray[0],bboxArray[1]], [bboxArray[2], bboxArray[3]] ];

      const flyToProps = _map.current.getMap().cameraForBounds(bbox, {
        padding: {top: 100, bottom: 100, left: 100, right: 100}
      });

      updateViewport({
        latitude: flyToProps.center.lat,
        longitude: flyToProps.center.lng,
        zoom: flyToProps.zoom - 0.5,
        transitionDuration: 1000,
        transitionInterpolator: new FlyToInterpolator(),
      });
    }
  }, [roofDrawed]);

  useEffect(() => {
    if(stepsList[1].isActive){
      updateViewport({
        ...marker,
        zoom: 20,
      });
    }
  }, [stepsList[1].isActive]);

  useEffect(() => {
    updateViewport({
      ...START_POINTS[startRegion].value,
      zoom: 7.5,
    });
  }, [startRegion]);
  

  useEffect(() => {
    setTimeout(() => { //crutch for redraw arrows layer after all data updating
        setRotatePoints(getRotatePoints({ index: stepsList[5].params.selectedFeatureIndex, lines: _bearingLines }));
    }, 100);
  }, [stepsList[5].params.selectedFeatureIndex]);

  useEffect(() => {
    editorFeatureUpdate({ updatedData: editorData, editType: "paramsChanged" });
  }, [stepsList[5].data.panelsData, stepsList[5].data.panelsPosition, stepsList[3].data.roofTilt]);

  useEffect(() => {
    if(selectedLineFeatureIndex !== null){
      editorFeatureUpdate({ updatedData: editorData, editType: "changeSelectedLine", editContext: { featureIndexes: [selectedLineFeatureIndex] } });
      setSelectedLineFeatureIndex(null);
    }
  }, [selectedLineFeatureIndex]);

  useEffect(() => {
    if(stepsList[5].params.action === MODES.ACTION.DELETE.id && stepsList[5].params.selectedFeatureIndex !== null){
      _doDelete(stepsList[5].params.selectedFeatureIndex);
    }
    setStepParams({ params: { action: null }, stepIndex: 5 });
  }, [stepsList[5].params.action]);

  const _getCursor = ({ isDragging }) => {
    let cursor = isDragging ? 'grabbing' : 'grab';

    if(_panelsHovering && stepsList[5].params.mode === "EDIT_PANELS"){ //_panelsHovering && 
      return cursor = 'pointer';//'url('+ (_panelsHovering === 'add' ? addImg : deleteImg) +') 9 9, not-allowed';
    }

    if(stepsList[5].params.mode === "EDIT"){
      return cursor = 'pointer';
    }

    if(stepsList[5].isActive && stepsList[5].params.mode === MODES.DRAW_TOOL.DRAW.id){
      cursor = 'url('+ cursorImg +'), crosshair';
    }

    return cursor;
  }

  const isDeletedPanel = (panel) => {
    const result = _.find(_deletedPanels, (delPan) => {
      return _.isEqual(delPan, panel);
    });
    return result !== undefined;
  }

  const _doDelete = (featureIndex) => {
    let newEditorData = editorData;
    newEditorData.features = _.filter(newEditorData.features, function(feature) { return feature.properties.key !== featureIndex; });
    setStepData({ data: { pvwatts: [] }, stepIndex: 5 });
    editorFeatureUpdate({ updatedData: newEditorData, editContext: { featureIndexes: [featureIndex] }, editType: "deletePolygon" });
    setStepParams({ params: { selectedFeatureIndex: null }, stepIndex: 5 });
    //setStepMode({ mode: "EDIT_PANELS", stepIndex: 5 });

    
    setSelectedLines({
      ..._.filter(_selectedLines, (line, key) => {
        return +featureIndex !== +key;
      }),
    });
    

    //deletePVWatts({ key: featureIndex, pvwattsList: stepsList[5].data.pvwatts });
  }

  const getBearingLines = ({ polygon, selectedLine }) => {
    const polygonCenter = turf.centerOfMass(polygon);

    let prevCoord = null;
    let allLines = [];
    let newSelectedLine = null;

    _.map(polygon.geometry.coordinates[0], (coord, key) => {
        if(prevCoord !== null) {
            let line = turf.lineString([prevCoord, coord]);
            const lineLength = turf.length(line);
            const lineBearing = turf.bearing(prevCoord, coord);
            let azimuthAngle = turf.bearingToAzimuth(lineBearing) + 90;
            const centroid = turf.centroid(line);

            if(azimuthAngle > 360){
              azimuthAngle = azimuthAngle - 360;
            }


            line.properties = {
              key,
              polygonKey: polygon.properties.key,
              length: lineLength,
              bearing: lineBearing,
              azimuth: azimuthAngle,
              centroid: centroid,
            };

            allLines.push(line);

            if(selectedLine && selectedLine.properties.key < polygon.geometry.coordinates[0].length){
              if(selectedLine.properties.key === key)
                newSelectedLine = line;
            } else {
              if(newSelectedLine === null) {
                  newSelectedLine = line;
              } else {
                  if(newSelectedLine.properties.length < line.properties.length){
                      newSelectedLine = line;
                  }
              }
            }
        }
        prevCoord = coord;
    });

    allLines = _.map(allLines, function(o) {
      if(o.properties.key === newSelectedLine.properties.key) {
        o.properties = {
          ...o.properties,
          //selected: true,  
        };
        return o;
      } else {
        return o;
      }
    });

    return {
      lines: allLines,
      selectedLine: newSelectedLine,
    };
  }

  const getRotatePoints = ({ lines, index }) => {
    let points = [];
    if(index !== null && lines[index]){
      points = _.map(lines[index], (f) => {
        const newF = f.properties.centroid;
        newF.properties = {
          ...f.properties,
          centroid: null,
        };
        return f.properties.centroid;
      });
    }
    return points;
  }

  const getLongestEdge = ({ polygon }) => {
    const polygonCenter = turf.centerOfMass(polygon);
    let longestEdgeLength = 0;
    let prevCoord = null;

    _.map(polygon.geometry.coordinates[0], (coord) => {
        if(prevCoord !== null) {
            const edge = turf.lineString([coord, polygonCenter]);
            const edgeLength = turf.length(edge);

            if(longestEdgeLength < edgeLength){
              longestEdgeLength = edgeLength;
            }
        }
        prevCoord = coord;
    });

    return longestEdgeLength;
  }

  const getPolygonBbox = ({ polygon }) => {
    const polygonCenter = turf.centerOfMass(polygon);
    const circleForBbox = turf.circle(polygonCenter, getLongestEdge({ polygon }));
    const polygonBbox = turf.bbox(circleForBbox);

    return polygonBbox;
  }

  const getSolarPanels = ({ polygon, panelsData }, existedData) => {
    if(panelsData === null){
      return {};
    }

    const polygonCenter = turf.centerOfMass(polygon);
    const bearingLines = getBearingLines({ polygon, selectedLine: _selectedLines[polygon.properties.key] });
    const polygonBbox = getPolygonBbox({ polygon });

    setBearingLines({
      ..._bearingLines,
      [polygon.properties.key]: bearingLines.lines,
    });

    if(polygon.properties.key === stepsList[5].params.selectedFeatureIndex){
      setTimeout(() => { //crutch for redraw arrows layer after all data updating
        setRotatePoints(
          _.map(bearingLines.lines, (f) => {
            const newF = f.properties.centroid;
            newF.properties = {
              ...f.properties,
              centroid: null,
            };
            return f.properties.centroid;
          })
        );
      }, 100);
    }

    let filteredGrid = [];

    if(!existedData){
      const panelsParams = {
        width: stepsList[5].data.panelsPosition === "Horizontal" ? panelsData.width/1000000 : panelsData.height/1000000, //1000000
        height: stepsList[5].data.panelsPosition === "Horizontal" ? panelsData.height/1000000 : panelsData.width/1000000,
      };
      const tiltedWidth = +stepsList[3].data.roofTilt !== 0 ? panelsParams.width*Math.cos(+stepsList[3].data.roofTilt * Math.PI / 180) : panelsParams.width;

      const squareGrid = rectangleGrid(polygonBbox, panelsParams.height, tiltedWidth, { properties: { type: "panels", featureKey: polygon.properties.key, azimuth: bearingLines.selectedLine.properties.azimuth } });
      let rotatedSquareGrid = turf.transformRotate(squareGrid, bearingLines.selectedLine.properties.azimuth, { pivot: polygonCenter.geometry.coordinates });

      _.map(rotatedSquareGrid.features, (f) => {
        let new_f = f;
        if(turf.booleanContains(polygon, f)) {
          new_f.properties.isActive = true;
          filteredGrid.push(new_f);
        }
      });
    } else {
      filteredGrid = existedData;
    }

    filteredGrid = turf.featureCollection(filteredGrid);

    const translateStep = 0.00001;
    let translateLength = translateStep;

    while (translateLength < 0.01) {
      const translated_filteredGrid = turf.transformTranslate(filteredGrid, translateLength, bearingLines.selectedLine.properties.azimuth);
      if(isPolygonContainsPanels(polygon, translated_filteredGrid)){
        filteredGrid = translated_filteredGrid;
        translateLength = translateLength + translateStep;
      } else {
        translateLength = 99999;
      }
    }

    setStepParams({ params: { panelsIsPlaced: true }, stepIndex: 5 });

    const PVWattsQuery = {
      coords: {
        lat: marker.latitude,
        lon: marker.longitude,
      },
      stepIndex: 5,
      system_capacity: stepsList[5].data.panelsData.watt/1000,
      module_type: 1,
      losses: 14,
      array_type: 1,
      tilt: +stepsList[3].data.roofTilt,
      azimuth: bearingLines.selectedLine.properties.azimuth,
      inv_eff: 96,
      key: polygon.properties.key,
      panels_count: _.filter(filteredGrid.features, (feature) => {
        return !isDeletedPanel(feature);
      }).length,
      panelsData: stepsList[5].data.panelsData,
      pvwattsList: stepsList[5].data.pvwatts,
    };

    return { filteredGrid, PVWattsQuery };
  }

  const isPolygonContainsPanels = (polygon, panels) => {
    for (var i = panels.features.length - 1; i >= 0; i--) {
      if(!turf.booleanContains(polygon, panels.features[i])){
        return false;
      }
    }

    return true;
  }

  const isRoofContainsPanels = (roofs, panels) => {
    for (var i = panels.features.length - 1; i >= 0; i--) {
      if(!turf.booleanContains(roofs.features[panels.features[i].properties.featureKey], panels.features[i])){
        return false;
      }
    }

    return true;
  }

  const isPanelIntersected = (panel, panels) => {
    for (var j = panels.length - 1; j >= 0; j--) {
      if(turf.booleanOverlap(panel, panels[j])){
        return true;
      }
    }

    return false;
  }

  const isPanelsIntersected = (inexes, panels) => {
    for (var i = inexes.length - 1; i >= 0; i--) {
      for (var j = panels.length - 1; j >= 0; j--) {
        if(inexes[i] !== j && turf.booleanOverlap(panels[inexes[i]], panels[j])){
          return true;
        }
      }
    }

    return false;
  }

  const editorFeatureTranslate = ({ updatedData, editType, editContext }) => {
    const isPanelsIntersectedBool = isPanelsIntersected(editContext.featureIndexes, updatedData.features);

    if(cachedPanelPosition === null){
      setCachedPanelPosition(panelsData);
    }

    if(isRoofContainsPanels(editorData, updatedData)){
      if(!isPanelsIntersectedBool){
        setCachedPanelPosition(updatedData);
      }
      if(editType === 'translated'){
        setPanelsData(isPanelsIntersectedBool ? cachedPanelPosition : updatedData);
        if(isPanelsIntersectedBool){
          setHelpMessage({ title: "Wrong placement", message: "Panels should not overlap." });
        }
      } else {
        setPanelsData(updatedData);
      }
    }
  }

  const editorFeatureUpdate = ({ updatedData, editType, editContext, newPanelsData }) => {
    if(!updatedData.features.length){
      setPanelsData({ type: 'FeatureCollection', features: [] });
      setStepData({ data: { pvwatts: {} }, stepIndex: 5 });
      return null;
    }

    let solarPanelsAll = [];
    let PVWattsQueries = [];

    _.map(updatedData.features, (feature, key) => {
      feature.properties.key = key;

      let existedData = null;

      if(editContext){
        if(editType === "addTentativePosition" || (editContext.featureIndexes && editContext.featureIndexes.indexOf(key) === -1)){
          existedData = _.filter(panelsData.features, f => f.properties.featureKey === key);
        }
        if(editType === "changeSelectedLine" && (editContext.featureIndexes && editContext.featureIndexes.indexOf(key) === -1)){
          existedData = _.filter(panelsData.features, f => f.properties.featureKey === key);
        }
      }

      if(editType === "panelsChanged"){
        existedData = _.filter(newPanelsData.features, f => f.properties.featureKey === key);
      }

      const {
        PVWattsQuery,
        filteredGrid,
      } = getSolarPanels({ polygon: feature, panelsData: stepsList[5].data.panelsData }, existedData);

      PVWattsQueries.push(PVWattsQuery);

      if(filteredGrid){
        solarPanelsAll = [
          ...solarPanelsAll,
          ...filteredGrid.features,
        ];
      }
    });

    setPVWattsQueries(PVWattsQueries);

    let panelsGeometryCollection = [];
    const filteredAllPanels = _.filter(solarPanelsAll, (el) => !isDeletedPanel(el));
    const directionPoint = _.find(_rotatePoints, function(o) { return o.properties.selected; });
    const mergedPanels = turf.multiPolygon(
        _.map(filteredAllPanels, (el) => {
          return el.geometry.coordinates;
        }),
        {
          "stroke": "#e10000",
          "stroke-width": 1,
          "stroke-opacity": 1,
          "fill": "#00b4e1",
          "fill-opacity": 0.5
        }
      );

    panelsGeometryCollection.push(mergedPanels);
    if(directionPoint){
      panelsGeometryCollection.push(directionPoint);
    }

    addLeadData({
      'PanelsCount': filteredAllPanels.length,
      'PanelsGeometry': JSON.stringify(turf.featureCollection(panelsGeometryCollection)),
    });

    setPanelsData({ type: 'FeatureCollection', features: solarPanelsAll });

    if(editType === 'addFeature' || editType === 'addPosition' || editType === 'finishMovePosition' || editType === 'removePosition'){
      const bearingLines = getBearingLines({ polygon: updatedData.features[updatedData.features.length - 1], selectedLine: _selectedLines[updatedData.features.length - 1] });

      setBearingLines({
        ..._bearingLines,
        [updatedData.features.length - 1]: bearingLines.lines,
      });

      setTimeout(() => { //crutch for redraw arrows layer after all data updating
        setRotatePoints(
          _.map(bearingLines.lines, (f) => {
            const newF = f.properties.centroid;
            newF.properties = {
              ...f.properties,
              centroid: null,
            };
            return f.properties.centroid;
          })
        );
      }, 100);
    }

    if(editType === 'addFeature'){
      setStepParams({ stepIndex: 5, params: { directionIsSelected: false } });
      setStepParams({ params: { selectedFeatureIndex: updatedData.features.length - 1 }, stepIndex: 5 });

      const messageTypeStr = stepsList[3].data.roofTilt === "20" ? "roof" : "panels";

      if(isShowHelpMessages){
        setHelpMessage({
          title: "Select direction",
          message: `Select direction of the ${messageTypeStr} by clicking on the arrow. If you need to modify the shape of the roof you can move the red dots or add/delete by clicking on them.`,
          buttonText: "Lets get started"
        });
      }

      setStepMode({ mode: MODES.DRAW_TOOL.EDIT.id, stepIndex: 5 });
      setRoofDrawed(updatedData);
    }

    setEditorData(updatedData);
  }

  const setMarkerPosition = (coords, zoom) => {
    if(turf.booleanPointInPolygon(turf.point(coords), _boundaryGeojson)){
        setStepData({ stepIndex: 0, data: { longitude: coords[0], latitude: coords[1] } });
        if(zoom){
          updateViewport({
            latitude: coords[1],
            longitude: coords[0],
            zoom: zoom,
            transitionDuration: 1000,
            transitionInterpolator: new FlyToInterpolator(),
          });
        } else {
          updateViewport({
            latitude: coords[1],
            longitude: coords[0],
            transitionDuration: 1000,
            transitionInterpolator: new FlyToInterpolator(),
          });
        }
        setMarker({
          longitude: coords[0],
          latitude: coords[1],
        });
      } else {
        setErrorMessage({ error: "Unattended area", message: "Sorry, but we're not servicing this area." });
        setStepMode({ mode: MODES.PIN.CHANGE.id, stepIndex: 0 });
        setActiveStep(0, { isFinished: false });
      }
    
  };

  const _onClick = event => {

    if(event.target && event.target.nodeName === "BUTTON") return null;

    if(event.layer === null){
      setSelectedPanels([]);
    }
    if(stepsList[5].params.mode === "EDIT_PANELS" && (event.layer && event.layer.id !== "roof-layer")){
      setSelectedPanels([]);
    }

    if(stepsList[5].params.addPanelsAction === true && newPanelData !== null){
      if(!event.layer || (event.layer && !(event.layer.id === "mesh-layer" || event.layer.id === "panels-layer" || event.layer.id === "roof-layer" || event.layer.id === "view-roof-layer"))){
        setHelpMessage({ title: "Wrong placement", message: "The panel must be placed inside the roof polygon." });
      } else {
        const featureKey = event.object.properties.key >= 0 ? event.object.properties.key : event.object.properties.featureKey;
        if(featureKey === stepsList[5].params.selectedFeatureIndex){
          const panelCopy = _.find(panelsData.features, (f) => f.properties.featureKey === stepsList[5].params.selectedFeatureIndex);
          if(panelCopy){
            const panelCopy_placed = turf.transformTranslate(panelCopy, turf.distance(turf.centroid(panelCopy), turf.point(newPanelData[0])), turf.bearing(turf.centroid(panelCopy), turf.point(newPanelData[0])));
            if(turf.booleanWithin(panelCopy_placed, editorData.features[stepsList[5].params.selectedFeatureIndex]) && !isPanelIntersected(panelCopy_placed, panelsData.features)){
              setTimeout(() => setSelectedPanelsUpdateTrigger(true), 500);
              setPanelsData((current_panelsData) => ({ type: 'FeatureCollection', features: [ ...current_panelsData.features, panelCopy_placed] }));
              setStepParams({ params: { addPanelsAction: false }, stepIndex: 5 });
              setSelectedPanels((current_selectedPanels) => ([ ...current_selectedPanels, panelsData.features.length ]));
              editorFeatureUpdate({ updatedData: editorData, editType: "panelsChanged", newPanelsData: { type: 'FeatureCollection', features: [ ...panelsData.features, panelCopy_placed] } });
            } else {
              setHelpMessage({ title: "Wrong placement", message: "The panel must be placed inside the selected side of roof and should not overlap." });
            }
          } else {
            setHelpMessage({ title: "Wrong placement", message: "Need more space. Please, expand the polygon area." });
          }
        } else {
          setHelpMessage({ title: "Wrong placement", message: "The panel must be placed inside the selected side of roof." });
        }
      }
    }

    if(event.layer && event.layer.id === "arrows-layer"){
      setSelectedLines({
        ..._selectedLines,
        [event.object.properties.polygonKey]: event.object,
      });
      setSelectedLineFeatureIndex(event.object.properties.polygonKey);
      
      if(stepsList[5].data.panelsData){
        setStepParams({ params: { directionIsSelected: true }, stepIndex: 5 });
        setStepMode({ mode: "EDIT_PANELS", stepIndex: 5 });
        if(isShowHelpMessages){
          setHelpMessage({ title: "Edit panels", message: "Select panels to edit by clicking on them. You can move or delete panels and add new ones." });
        }
      } else {
        setStepParams({ stepIndex: 5, params: { directionIsSelected: true } });
        if(isShowHelpMessages){
          setHelpMessage({ title: "SELECT YOUR BRAND OF PANELS", message: "Select the brand and wattage of the panels you prefer." });
        }
      }
    }

    if(stepsList[5].isActive && stepsList[5].params.mode === MODES.DRAW_TOOL.EDIT.id){
      if(event.layer && ((event.layer.id === "roof-layer" && event.object && (event.object.properties.key >= 0)) || (event.layer.id === "panels-layer" && event.object && (event.object.properties.featureKey >= 0))) ){
        setStepParams({ params: { selectedFeatureIndex: event.object.properties.key >= 0 ? event.object.properties.key : event.object.properties.featureKey }, stepIndex: 5 });
      }
    }
    if(stepsList[0].isActive && stepsList[0].params.mode === MODES.PIN.CHANGE.id){
      setMarkerPosition(event.lngLat);
      setStepMode({ mode: MODES.PIN.CONFIRM.id, stepIndex: 0 });
    }
  }

  const _onMarkerDragEnd = event => {
    setMarkerPosition(event.lngLat);
    setStepMode({ mode: MODES.PIN.CONFIRM.id, stepIndex: 0 });
  };

  const updateViewport = viewport => {
    if(_boundaryGeojson){
      if(turf.booleanPointInPolygon(turf.point([viewport.longitude, viewport.latitude]), _boundaryGeojson)){
        mapboxViewportChange(viewport);
      }
    } else {
      mapboxViewportChange(viewport);
    }
  };

  const editorLayerProps = {
    onEdit: editorFeatureUpdate,
    _subLayerProps: {
      geojson: {
        getLineColor: (feature) => {
          if (stepsList[5].params.selectedFeatureIndex === feature.properties.key) {
            return [0, 180, 255, 255];
          } else {
            return [0, 180, 255, 255];
          }
        },
        getFillColor: (feature) => [102, 113, 128, 150],
      }
    }
  };

  const editorLayer = new EditableGeoJsonLayer({
    ...editorLayerProps,
    id: 'roof-layer',
    data: editorData,
    mode: DrawPolygonMode,
    selectedFeatureIndexes: [],
  });

  const modifyLayer = new EditableGeoJsonLayer({
    ...editorLayerProps,
    id: 'roof-layer',
    data: editorData,
    pickingRadius: 20,
    mode: ModifyMode,
    selectedFeatureIndexes: (stepsList[5].params.selectedFeatureIndex !== null ? [stepsList[5].params.selectedFeatureIndex] : []),
  });

  const viewRoofLayer = new EditableGeoJsonLayer({
    ...editorLayerProps,
    onEdit: () => null,
    id: 'view-roof-layer',
    data: editorData,
    mode: ViewMode,
    _subLayerProps: {
      geojson: {
        getLineColor: (feature, data) => {
          if (feature.properties.key === stepsList[5].params.selectedFeatureIndex) {
            return [0, 180, 255, 255];
          } else {
            return [0, 0, 0, 50];
          }
        },
        getFillColor: (feature, data) => {
          if (feature.properties.key === stepsList[5].params.selectedFeatureIndex) {
            return [102, 113, 128, 150];
          } else {
            return [0, 0, 0, 50];
          }
        },
      }
    },
    selectedFeatureIndexes: [],
  });

  const translateLayer = new EditableGeoJsonLayer({
    ...editorLayerProps,
    onEdit: editorFeatureTranslate,
    id: 'roof-layer',
    data: panelsData,
    pickingRadius: 20,
    mode: TranslateMode,
    onClick: (info, e) => {
      if(stepsList[5].params.mode === "EDIT_PANELS" && info.object.properties.featureKey === stepsList[5].params.selectedFeatureIndex){
        setSelectedPanelsUpdateTrigger(true);
        setSelectedPanels((current_selectedPanels) => {
          const searched_current_selectedPanels = _.findIndex(current_selectedPanels, (id) => id === info.index);
          let new_current_selectedPanels = current_selectedPanels;
          if (searched_current_selectedPanels > -1) {
            new_current_selectedPanels.splice(searched_current_selectedPanels, 1);
          } else {
            new_current_selectedPanels.push(info.index);
          }
          return new_current_selectedPanels;
        });
      }
    },
    _subLayerProps: {
      geojson: {
        getLineColor: (feature, data) => {
          if (_.findIndex(selectedPanels, (id) => id === data.index) > -1) {
            return [0, 180, 255, 255];
          } else {
            return [0, 180, 255, 0];
          }
        },
        getFillColor: (feature, data) => {
          if (_.findIndex(selectedPanels, (id) => id === data.index) > -1) {
            return [102, 113, 128, 0];
          } else {
            return [102, 113, 128, 0];
          }
        },
      }
    },
    selectedFeatureIndexes: selectedPanels,
  });

  const meshAddPanelLayer = new SimpleMeshLayer({
    id: 'mesh-layer-addpanel',
    data: newPanelData,
    pickable: false,
    getOrientation: d => {
      if(panelsData.features.length === 0 || stepsList[5].params.selectedFeatureIndex === null){
        return [0, 0, 0];
      }
      const panelCopy = _.find(panelsData.features, (f) => f.properties.featureKey === stepsList[5].params.selectedFeatureIndex);
      if(!panelCopy){
        return [0, 0, 0];
      }
      const coords = panelCopy.geometry.coordinates[0];
      const line1 = turf.lineString([coords[0],coords[1]]);
      const line2 = turf.lineString([coords[1],coords[2]]);
      const longestSide = turf.length(line1) > turf.length(line2) ? line2 : line1;
      const centerOfSide = turf.centerOfMass(longestSide);
      const bearing = turf.bearing(turf.centerOfMass(panelCopy), centerOfSide);
      return [0, -(bearing + 180), 0];
    },
    getPosition: d => {
      return d;
    },
    getColor: [255, 255, 255],
    getScale: d => {
      return [1, 1, 1];
    },
    texture: panelPatternImg,
    mesh: stepsList[5].data.panelsData && new PlaneGeometry({
      type: 'x,y',
      xlen: ((stepsList[5].data.panelsData.height - 10) / 1000),
      ylen: ((stepsList[5].data.panelsData.width - 110) / 1000),
      nx: 5,
      nz: 5,
      offset: 0
    })
  });

  const meshLayer = new SimpleMeshLayer({
    id: 'mesh-layer',
    data: panelsData.features,
    pickable: false,
    getOrientation: d => {
      const coords = d.geometry.coordinates[0];
      const line1 = turf.lineString([coords[0],coords[1]]);
      const line2 = turf.lineString([coords[1],coords[2]]);
      const longestSide = turf.length(line1) > turf.length(line2) ? line2 : line1;

      const centerOfSide = turf.centerOfMass(longestSide);

      const bearing = turf.bearing(turf.centerOfMass(d), centerOfSide);

      return [0, -(bearing + 180), 0];
    },
    getPosition: d => {
      return turf.center(d).geometry.coordinates;
    },
    getColor: [255, 255, 255],
    getScale: d => {
      if(isDeletedPanel(d)){
        return [0, 0, 0];
      } else {
        return [1, 1, 1];
      }
    },
    texture: panelPatternImg,
    mesh: stepsList[5].data.panelsData && new PlaneGeometry({
      type: 'x,y',
      xlen: ((stepsList[5].data.panelsData.height - 10) / 1000),
      ylen: ((stepsList[5].data.panelsData.width - 110) / 1000),
      nx: 5,
      nz: 5,
      offset: 0
    })
  });

  const panelsLayer = new PolygonLayer({
    id: 'panels-layer',
    data: panelsData.features,
    _normalize: true,
    pickable: true,
    stroked: true,
    filled: true,
    getPolygon: d => {
      return d.geometry.coordinates;
    },
    getFillColor: d => [0, 180, 255, 0],
    onClick: (info, e) => {
      if(stepsList[5].params.mode === "EDIT_PANELS"){
        if(isDeletedPanel(info.object)){
          setDeletedPanels(_.filter(_deletedPanels, (delPan) => !_.isEqual(info.object, delPan)));
        } else {
          setDeletedPanels([
            ..._deletedPanels,
            info.object,
          ]);
        }
      }
    },
    autoHighlight: (stepsList[5].params.mode === "EDIT_PANELS"),
    highlightColor: [255, 0, 0, 150],
    getLineColor: (d) => {
      if(isDeletedPanel(d)){
        return [0, 0, 0, 0];
      } else {
        return [255, 255, 255, 255];
      }
    },
    getLineWidth: 4,
    lineWidthMinPixels: 1,
    lineWidthMaxPixels: 4,
    lineWidthScale: 0,
    lineWidthUnits: 'pixels',
    updateTriggers: {
        getFillColor: [_deletedPanels],
        getLineWidth: [_deletedPanels],
    }
  });

  const ICON_MAPPING = {
    marker: {x: 0, y: 0, width: 64, height: 64, mask: true, anchorY: 128,}
  };

  const arrowsLayer = new IconLayer({
    id: 'arrows-layer',
    data: _rotatePoints,
    pickable: true,
    iconAtlas: arrowImg,
    iconMapping: ICON_MAPPING,
    getIcon: d => 'marker',
    getPosition: d => d.geometry.coordinates,
    sizeUnits: 'meters',
    getSize: d => 1.5,
    sizeMaxPixels: 32,
    getAngle: d => -d.properties.azimuth,
    sizeMinPixels: 5,
    getColor: d => {
      let color = [0, 180, 255, 255];
      if(_selectedLines[d.properties.polygonKey]){
        if(d.properties.key === _selectedLines[d.properties.polygonKey].properties.key) color = [102, 113, 128, 180];
      } else {
        if(d.properties.selected) color = [102, 113, 128, 180];
      }

      return color;
    },
    updateTriggers: {
        getColor: [_selectedLines]
    }
  });

  return (
      <ReactMapGL
        ref={_map}
        mapboxApiAccessToken = { mapboxApiAccessToken }
        {...viewport}
        onViewportChange={(viewport) => updateViewport(viewport)}
        onLoad={() => mapboxReady()}
        minZoom={(stepsList[0].isActive ? 7 : 18)}
        onMouseMove={event => {
          if(stepsList[5].params.addPanelsAction === true){
            setNewPanelData([event.lngLat]);
          }
        }}
        onClick={event => (currentStep < 1) && _onClick(event)}
        dragPan={!(stepsList[5].params.mode === "EDIT" || stepsList[5].params.mode === "EDIT_PANELS")}
      >
        <DeckGL
          ref={ref => _deck = ref}
          initialViewState = {{
            ...viewport,
          }}
          controller={{type: MapController, dragRotate: true, dragPan: !(stepsList[5].params.mode === "EDIT" || stepsList[5].params.mode === "EDIT_PANELS")}}
          layers={[
            stepsList[5].params.mode === "DRAW" && stepsList[5].params.action !== MODES.ACTION.DELETE.id ? editorLayer : null,
            stepsList[5].params.mode === "EDIT" && stepsList[5].params.action !== MODES.ACTION.DELETE.id ? modifyLayer : null,
            stepsList[5].params.mode === "EDIT_PANELS" && stepsList[5].params.action !== MODES.ACTION.DELETE.id ? viewRoofLayer : null,
            meshLayer,
            panelsLayer,
            stepsList[5].params.mode === "EDIT_PANELS" && stepsList[5].params.action !== MODES.ACTION.DELETE.id && !selectedPanelsUpdateTrigger ? translateLayer : null,
            stepsList[5].params.mode === "EDIT" && stepsList[5].params.action !== MODES.ACTION.DELETE.id ? arrowsLayer : null,
            stepsList[5].params.addPanelsAction === true && newPanelData !== null ? meshAddPanelLayer : null,
          ]}
          getTooltip={({object}) => {
            if(object && ((object.properties.type === "panels" && object.properties.featureKey !== null) || (object.geometry.type === "Point" && object.properties.azimuth))){
              setPanelsHovering('selectPanel');
            } else {
              setPanelsHovering(false);
            }
          }}
          onClick={event => (currentStep > 1) && _onClick(event)}
          getCursor={_getCursor}
        />
        {stepsList[0].isActive && stepsList[0].params.mode === MODES.PIN.CONFIRM.id && marker &&
          <Popup
            latitude={marker.latitude}
            longitude={marker.longitude}
            closeButton={false}
            closeOnClick={false}
            captureClick={false}
            anchor="bottom"
          >
            <div className="address-popup">Is this your property?</div>
            <div className="popup-buttons">
              <Button onClick={() => {
                addLeadData({ 'Coordinates': JSON.stringify(marker) });
                setActiveStep(1);
              }}>YES</Button>
              <Button onClick={() => {
                setStepMode({ mode: MODES.PIN.CHANGE.id, stepIndex: 0 });
              }}>NO</Button>
            </div>
          </Popup>
        }
        {stepsList[0].isActive && stepsList[0].params.mode === MODES.PIN.CHANGE.id && marker &&
          <Marker
            latitude={marker.latitude}
            longitude={marker.longitude}
            offsetLeft={-33}
            offsetTop={-75}
            draggable={true}
            onDragEnd={_onMarkerDragEnd}
          >
            <div className="map-marker">
              <img src={pinImg} width={64} draggable="false" />
            </div>
          </Marker>
        }
        {_boundaryGeojson &&
          <Source id="boundary-data" type="geojson" data={turf.mask(_boundaryGeojson)}>
            <Layer
              id="boundary-layer"
              type="fill"
              paint={{
                'fill-color': '#000',
                'fill-opacity': 0.5,
              }}
            />
          </Source>
        }
        <div className="navControl" style={{zIndex: '1'}}>
          <NavigationControl
            showCompass={false}
          />
        </div>
      </ReactMapGL>
  );
}

const mapStateToProps = state => {
  return {
  	viewport: state.mapbox.viewport,
    geocodeResult: state.mapbox.geocodeResult,
    stepsList: state.steps.list,
    currentStep: state.steps.currentStep,
    isMapboxReady: state.mapbox.isMapboxReady,
    startRegion: state.mapbox.startRegion,
  };
};

export default connect(
  mapStateToProps,
  { mapboxViewportChange, mapboxReady, setStepParams, setStepMode, setStepData, getPVWatts, setActiveStep, deletePVWatts, addLeadData, setMapboxRef, setErrorMessage, setHelpMessage },
)(MapBox);